.btn{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;

  padding: 15px 55px 15px 37px;
  border-radius: 22px;
  border: 2px solid transparent;

  font-size: 16px;
  line-height: calc(21 / 16);
  color: $title-color;
  font-weight: 700;

  background-color: $white;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1), 
  color 250ms cubic-bezier(0.4, 0, 0.2, 1),
  border 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover{
    color: $white;
    background-color: $btn-color;

    .btn__arrow{
      &::before,
      &::after{
        background-color: $white;
      }
    }
  }

  &__arrow{
    position: relative;
    &::before,
    &::after{
      content: "";
      position: absolute;
      width: 8px;
      height: 2px;
      border-radius: 20px;
      background-color: $btn-color;
    }
    
    &::before{
      bottom: 1px;
      right: -20px;
      transform: rotate(40deg);
    }

    &::after{
      top: 1px;
      right: -20px;
      transform: rotate(-40deg);
    }
  }

  

  &--active{
    color: $white;
    background-color: $btn-color;

    .btn__arrow::before,
    .btn__arrow::after{
      background-color: $white;
    }

    &:hover{
      color: $btn-color;
      background-color: $white;
      border-color: $btn-color;

      .btn__arrow::before,
      .btn__arrow::after{
        background-color: $btn-color;
      }
    }
  }
}